body {
  margin: 0;
  background-repeat: no-repeat;
  background-position: center 180px;
  overflow-x: hidden;
  font-family: Roboto; }

.app-container {
  min-height: 100vh; }

.header-container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 45px;
  max-width: 1350px;
  margin: 40px auto 0; }

.succes {
  background-image: url(/static/media/success.3a079626.jpg); }

.failure {
  background-image: url(/static/media/failure.ef048b74.jpg); }

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px 15px;
  max-width: 1440px;
  margin: auto; }

.container h2 {
  width: 100%;
  font-size: 40px;
  margin: 0;
  text-align: center;
  font-family: Roboto;
  text-transform: uppercase;
  background: -webkit-gradient(linear, left top, right top, from(#feb46b), to(#fd6a63));
  background: linear-gradient(90deg, #feb46b, #fd6a63);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #fd6a63; }

@media (max-width: 1430px) {
  .header-container {
    max-width: 1000px; } }

@media (max-width: 1080px) {
  .header-container {
    max-width: 650px; } }

@media (max-width: 730px) {
  .header-container {
    max-width: 300px; } }

@media (max-width: 430px) {
  body {
    background-size: 300px 300px; } }

.card audio {
  width: 300px;
  height: 250px;
  background-color: #000;
  display: block !important; }

.card-header {
  margin-top: 200px;
  padding: 0 15px;
  font-family: "Roboto";
  font-size: 24px;
  line-height: 45px;
  text-align: center;
  width: 100%;
  pointer-events: none;
  background: #fff;
  color: #212529; }

.back .card-header {
  background: #fff;
  color: #000; }

.card-header.active {
  background: linear-gradient(40deg, #ffd86f, #fc6262);
  color: #fff; }

.inactive {
  opacity: .5; }

.rotate {
  position: absolute;
  width: 50px;
  height: 50px;
  background-image: url(/static/media/rotate.229f42dc.png);
  background-size: 32px;
  background-repeat: no-repeat;
  background-position: center 16px;
  bottom: 5px;
  right: 5px;
  -webkit-transition: .3s;
  transition: .3s;
  -webkit-filter: grayscale(1);
  filter: grayscale(1); }

.rotate:hover {
  -webkit-filter: none;
  filter: none; }

.card-container {
  -webkit-perspective: 500px;
  perspective: 500px; }

.back,
.front,
.rotate {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden; }

.back {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg); }

.card {
  position: relative;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  display: flex;
  box-sizing: border-box;
  justify-content: flex-end;
  width: 300px;
  height: 250px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 20px 25px;
  box-shadow: 0 1px 3px 0 #ccc;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.4);
  -webkit-transition: box-shadow .25s linear, -webkit-transform .7s;
  transition: box-shadow .25s linear, -webkit-transform .7s;
  transition: box-shadow .25s linear, transform .7s;
  transition: box-shadow .25s linear, transform .7s, -webkit-transform .7s; }

.card:hover {
  box-shadow: -1px 5px 15px 0 rgba(0, 0, 0, 0.5); }

.translate {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg); }

.back,
.front {
  display: flex;
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  background-size: contain;
  background-repeat: no-repeat;
  -webkit-transition: .3s;
  transition: .3s; }

.card-cover .back,
.card-cover .front {
  background-size: cover;
  background-position: 50%; }

.btns {
  width: 100%;
  text-align: center; }

.btn {
  box-sizing: border-box;
  padding: 20px 25px;
  font-size: 24px;
  margin: 20px auto;
  width: 100%;
  max-width: 650px;
  height: 65px;
  background: linear-gradient(40deg, #ffd86f, #fc6262);
  color: #fff;
  border-radius: 10px;
  outline: 0 !important;
  border-width: 1px;
  -webkit-transition: .3s;
  transition: .3s; }

.repeat {
  width: 65px;
  font-size: 0;
  background-image: url(/static/media/repeat.d0f11196.svg), linear-gradient(40deg, #ffd86f, #fc6262);
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 32px 32px, cover;
  background-position: 50%; }

.none {
  box-sizing: border-box;
  opacity: 0;
  width: 0;
  height: 0;
  font-size: 0;
  margin: 0;
  padding: 0; }

.rating {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  max-width: 1350px;
  margin: 0 auto;
  height: 40px;
  overflow-x: hidden;
  line-height: 36px;
  font-size: 36px;
  color: #fc6262; }

.star-error {
  background-image: url(/static/media/star.dc1ba888.svg); }

.star-error,
.star-succes {
  flex-shrink: 0;
  width: 40px;
  min-width: 40px;
  height: 40px;
  background-size: 40px 40px; }

.star-succes {
  background-image: url(/static/media/star-win.43e453a6.svg); }

@media (max-width: 730px) {
  .btn {
    width: 300px; } }

@media (max-width: 430px) {
  .card {
    margin: 20px 0 !important; } }

.example-enter {
  opacity: .01; }

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in; }

.example-leave {
  opacity: 1; }

.example-leave.example-leave-active {
  opacity: .01;
  -webkit-transition: opacity .3s ease-in;
  transition: opacity .3s ease-in; }

nav {
  position: relative; }

.header-item {
  font-family: Roboto;
  padding: 10px;
  margin: 0 0 0 60px;
  color: #fff;
  min-width: 145px;
  text-align: left; }

.header-item.active {
  text-decoration: underline; }

.menuToggle {
  top: 11px;
  left: 0;
  z-index: 3;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

.menuToggle,
.menuToggle input {
  display: block;
  position: absolute; }

.menuToggle input {
  width: 40px;
  height: 32px;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none; }

.menuToggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #cdcdcd;
  border-radius: 3px;
  z-index: 1;
  -webkit-transform-origin: 4px 0;
  transform-origin: 4px 0;
  -webkit-transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }

.menuToggle span:first-child {
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0; }

.menuToggle span:nth-last-child(2) {
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%; }

.menuToggle input:checked ~ span {
  opacity: 1;
  -webkit-transform: rotate(45deg) translate(-2px, -1px);
  transform: rotate(45deg) translate(-2px, -1px);
  background: #cdcdcd; }

.menuToggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  -webkit-transform: rotate(0deg) scale(0.2);
  transform: rotate(0deg) scale(0.2); }

.menuToggle input:checked ~ span:nth-last-child(2) {
  opacity: 1;
  -webkit-transform: rotate(-45deg) translateY(-1px);
  transform: rotate(-45deg) translateY(-1px); }

.menu {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: absolute;
  width: 380px;
  height: calc(100vh + 20px);
  margin: -100px 0 0;
  padding: 125px 50px 50px;
  left: calc(675px - 50vw);
  background: linear-gradient(40deg, #ffd86f, #fc6262);
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: translate(-100%);
  transform: translate(-100%);
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }

.menu.green {
  background: linear-gradient(40deg, #00bf82, #0099ae); }

.header-item {
  padding: 10px 0;
  font-size: 22px;
  cursor: pointer;
  text-decoration: none; }

.menuToggle input:checked ~ ul {
  -webkit-transform: scale(1);
  transform: scale(1);
  opacity: 1; }

@media (max-width: 1430px) {
  .menu {
    left: calc(500px - 50vw); } }

@media (max-width: 1080px) {
  .menu {
    left: calc(325px - 50vw); } }

@media (max-width: 730px) {
  .menu {
    width: 320px;
    left: calc(150px - 50vw); }
  .header-item {
    margin-left: 40px; } }

